"use client";

import { ReactNode, useEffect } from "react";
import { Provider } from "react-redux";
import { store } from "@/services/store";
import { useDispatch } from "react-redux";
import { hydrateAuth } from "@/services/store/modules/authSlice";
import { ConfirmationProvider } from "./confirmation-provider";

export const ClientProviders = ({ children }: { children: ReactNode }) => {
  return (
    <Provider store={store}>
      <HydrateAuth>{children}</HydrateAuth>
    </Provider>
  );
};

const HydrateAuth = ({ children }: { children: ReactNode }) => {
  const dispatch = useDispatch();

  useEffect(() => {
    const userId = localStorage.getItem("userId");
    dispatch(hydrateAuth(userId));
  }, [dispatch]);

  return <>{children}</>;
};
